img {
  max-width: 200px;
  height: auto;
  border: 5px solid black;
  object-fit: cover;

  &:hover {
    transform: scale(2);
    transition: ease-in-out 0.5s;
  }
}

section {
  display: flex;
  flex-direction: row;
  margin: 2rem 4rem;
  background-color: #000;
  box-shadow: 3px 3px 3px 3px #0d0d0d;
  border-radius: 45px;
  justify-content: center;
  align-items: center;

  .culinary-text,
  .travel-text {
    flex: 1;
    justify-content: center;
    align-content: center;
    padding: 0 2rem;
    text-align: center;
  }

  aside {
    flex: 1;
    justify-content: center;
    align-content: center;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    border: 10px solid black;
    background-color: black;
    border-radius: 45px;

    >div {
      display: flex;
    }

    &.travel-images {
      grid-template-rows: 150px 150px;
    }

    &.food-images {
      grid-template-rows: 175px 175px 175px;
    }
  }
}

@media (max-width: 1200px) {
  section {
    flex-direction: column;
  }

  .culinary-text {
    order: -1;
  }
}
